/* GLOBAL STYLES */

* {
    padding: 0;
    margin: 0;
}

html {
    font-size: 62.5%;
}

a {
    text-decoration: none;
    color: inherit;
}
img {
    width: 100%;
    max-width: 100%;
    height: auto;
    display: block;
}

/* TYPOGRAPHY */
h1,
h3,
input {
  font-family: 'Bebas Neue', cursive;
}

h1 {
  font-size: 4rem;
}

/* HEADER STYLING  */
.fa-mask {
  font-size: 3rem;
  color: #43aa8b;
  margin-left: 5px;
}

.second-row {
  display: flex;
  justify-content: center;
}

h1 {
  padding-top: 20px;
  padding-bottom: 15px;
}

button {
  background-color: #c5e8dd;
}

.reload {
  padding: 7px 20px;
}

.reload-button {
  margin-right: 5px;
  margin-bottom: 20px;
}

input,
.search-btn {
  padding: 6px 20px;
  position: relative;
}

h4 {
  margin-top: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

span {
  color: #43aa8b;
}

/* MAIN STYLING */
.main-container {
  display: flex;
}

.header-container {
  position: fixed;
  background-color: #f8f9fa;
  height: 100vh;
  width: 300px;
  text-align: center;
}

h3 {
  font-size: 1.7rem;
}

.social-media {
  margin-top: 30px;
}

.social-media i {
  font-size: 4rem;
  padding: 5px;
}

.third-row {
  position: absolute;
  top: 80%;
  margin-left: 30px;
}

/* HOVER FOCUS STATES */
span:hover,
span:focus {
  text-decoration: #43aa8b 3px underline;
}

button:hover,
button:focus {
  background-color: goldenrod;
}

.social-media i:hover,
.social-media i:focus {
  color: #43aa8b;
}

.read:hover {
  background-color: goldenrod;
}

/* DYNAMIC STYLES */
.container {
  margin-left: 28%;
  margin-top: 20px;
  margin-bottom: 60px;
  margin-right: 15px;
} 

.tile-container {
  padding: 20px 30px;
  background-color: #c5e8dd;
  margin-bottom: 30px;
  border: 1px dotted grey;
  border-radius: 3%;
}  

h2 {
  font-size: 3rem;
  margin-top: 7px;
  margin-bottom: 5px;
} 

h5 {
  font-size: 1.3rem;
  margin-top: 7px;
  margin-bottom: 15px;
}

p {
  color: #495057;
  margin-bottom: 25px;
}

.read-div {
  align-items: center;
  display: flex;
  justify-content: center;
}

.read {
  background-color: #43aa8b;
  padding: 10px 30px;
  border-radius: 5%;
  color: black;
  font-weight: bolder;
  font-size: 1.8rem;
  margin-bottom: 25px;
}

/* MEDIA QUERIES */
@media (max-width: 1087px) {
  h1 {
    font-size: 5.2rem;
  }
  
  .main-container {
    display: flex;
    flex-direction: column;
  }

  .header-container {
    position: static;
    height: 60vh;
    width: 100%;
  }

  .third-row {
    position: static;
    margin-left: 0px;
  }

  .reload-button {
    margin-bottom: 0;
  }

  .credits {
    margin-top: 38px;
  }

  .container {
    margin-left: 15px;
  }
}

@media (max-width: 551px) {

  .header-container {
    position: static;
    height: 45vh;
    width: 100%;
  }

  h1 {
    font-size: 4rem;
  }
  h2 {
    font-size: 2.5rem;
  }

  i .fa-twitter-square,
  i .fa-linkedin,
  i .fa-facebook-square,
  i .fa-instagram-square
  {
    font-size: 5px;
  }

   .credits {
    margin-top: 8px;
  }

  h3 {
    display: none;
  }

  .read {
    padding: 8px 25px;
  }
}